import * as React from 'react';
import {useEffect, useState} from 'react';
import {Pagination, Stack} from "@mui/material";
import {useSearchParams} from "react-router-dom";
import {queryNodeTree, querySlice} from "./service/NodeTreeStoreService";
import SearchIcon from '@mui/icons-material/Search';
import {
  NodeTreeProfile,
  SortType
} from "../../proto/application/nodetreestore/NodeTreeStoreMessage";
import ClearableTextField from "../../component/input/ClearableTextField";
import MenuButton from "../../component/button/MenuButton";
import ContentButton from "../../component/button/ContentButton";
import {NodeTreeItemProfile} from "./component/NodeTreeItemProfile";
import Grid from '@mui/material/Grid2';
import {PageQuery} from "../../proto/framework/QueryMessage";
import {queryCustomerProfile} from "../customer/service/CustomerService";
import ProfileAvatar from "../customer/component/ProfileAvatar";
import {CustomerProfile} from "../../proto/framework/customer/CustomerInfo";
import {print} from "../../api/util/Logging";
import {
  getDownloadCount,
  getEngineLabel,
  getLikeCount,
  getViewCount,
  typeToLabel
} from "./NodeTreeStoreUtil";
import {useAppSelector} from "../../api/redux/hooks";
import {selectLoginResponse} from "../../api/redux/slice/SettingSlice";
import ContextMenu from "../../component/menu/ContextMenu";
import {useSmartNavigate} from "../../hook/CommonUiHook";
import {get_customer_url} from "../../navigation/routerUtil";

type Props = {};
const NodeTreeStoreWidget = (props: Props) => {
  const navigate= useSmartNavigate()
  const [searchParams, setSearchParams] = useSearchParams();
  // const
  const query = searchParams.get("q") ?? "";
  const owner = searchParams.get("o") ?? "";
  const type = searchParams.get("t") ?? "";
  const engine = searchParams.get("e") ?? "";
  const currentPage = searchParams.get("p") ? parseInt(searchParams.get("p") ?? "0") : 0;
  const pageSize = 40;

  const loginResponse = useAppSelector(selectLoginResponse);

  const [currentQuery, setCurrentQuery] = useState(query);

  const [nodeTrees, setNodeTrees] = React.useState<NodeTreeProfile[]>([]);

  const [totalPages, setTotalPages] = React.useState<number>(1);

  const [engineList, setEngineList] = React.useState<string[]>([]);
  const [typeList, setTypeList] = React.useState<string[]>([]);
  const [sortType, setSortType] = React.useState<SortType>(SortType.DEFAULT);


  const [customerProfile, setCustomerProfile] = React.useState<CustomerProfile | null>(null);
  useEffect(() => {
    if (owner === "") {
      return;
    }
    queryCustomerProfile(owner).then(res => {
      setCustomerProfile(res);
    }).catch(e => {
      print(e);
    });
  }, [owner]);

  useEffect(() => {
    querySlice().then(res => {
      setEngineList(res.engines);
      setTypeList(res.types);

    });
  }, []);

  useEffect(() => {
    const pageQuery = PageQuery.create();
    pageQuery.page = currentPage;
    pageQuery.pageSize = pageSize;

    queryNodeTree(query, owner, type, engine, pageQuery, sortType).then(res => {
      setNodeTrees(sortNodeTrees(res.nodeTrees));
      setTotalPages(res.pageResult?.totalPage ?? 1);
    });
  }, [searchParams, query, engine, type, owner, currentPage, sortType]);

  const sortNodeTrees = (nodeTrees: NodeTreeProfile[]) => {
    switch (sortType) {
      case SortType.DEFAULT:
        return nodeTrees;
      case SortType.UPDATE:
        return nodeTrees.sort((a, b) => {
          return (b.updateAt ?? 0) - (a.updateAt ?? 0);
        })
      case SortType.VIEW:
        return nodeTrees.sort((a, b) => {
          return (getViewCount(b)) - (getViewCount(a));
        })
      case SortType.LIKE:
        return nodeTrees.sort((a, b) => {
          return getLikeCount(b) - getLikeCount(a);
        })
      case SortType.DOWNLOAD:
        return nodeTrees.sort((a, b) => {
          return getDownloadCount(b) - getDownloadCount(a);
        })
      default:
        return nodeTrees;
    }
  }

  const engineToTitle = (str: string) => {
    if (str === "") {
      return "All Engines";
    }
    return getEngineLabel(str);
  }

  const getEngineMenuItems = () => {
    return ["", ...engineList].map(engine => {
      return <ContentButton
          title={engineToTitle(engine)}
          onClick={() => {
            setSearchParams((searchParams) => {
              searchParams.set("e", engine);
              return searchParams;
            })
          }}
      ></ContentButton>;
    })
  }

  const typeToTitle = (str: string) => {
    if (str === "") {
      return "All Node Types";
    }
    return typeToLabel(str);
  }

  const getTypeMenuItems = () => {
    return ["", ...typeList].map(type => {
      return <ContentButton
          title={typeToTitle(type)}
          onClick={() => {
            setSearchParams((searchParams) => {
              searchParams.set("t", type);
              return searchParams;
            })
          }}
      ></ContentButton>;
    });
  }

  const getUserMenuLabel = () => {
    if (owner === loginResponse?.userId) {
      return "Only Me";
    }
    return "All Users"
  }

  const getUserMenuItems = () => {
    return [
      <ContentButton title={"All Users"} onClick={() => {
        setSearchParams((searchParams) => {
          searchParams.set("o", "");
          return searchParams;
        })

      }}></ContentButton>,
      <ContentButton title={"Only Me"} onClick={() => {
        setSearchParams((searchParams) => {
          searchParams.set("o", loginResponse?.userId ?? "");
          return searchParams;
        })
      }}></ContentButton>,
    ]
  }

  const sortTypeToTitle = (sortType: SortType) => {
    switch (sortType) {
      case SortType.DEFAULT:
        return "Default";
      case SortType.UPDATE:
        return "Update Time";
      case SortType.VIEW:
        return "View Count";
      case SortType.LIKE:
        return "Like Count";
      case SortType.DOWNLOAD:
        return "Download Count";
      default:
        return "Default";
    }
  }

  const getSorTypeMenuItems = () => {
    return [SortType.DEFAULT, SortType.UPDATE, SortType.VIEW, SortType.LIKE, SortType.DOWNLOAD].map(sortType => {
      return <ContentButton
          title={sortTypeToTitle(sortType)}
          onClick={() => {
            setSortType(sortType);
          }}
      ></ContentButton>;
    });
  }


  return <Stack direction={"column"} width={"100%"} alignItems={"center"}>

    <Stack direction={"column"} spacing={1} sx={{
      width: {
        xs: "95%",
        sm: "90%",
        md: "80%",
        lg: "80%",
        xl: "80%",
      },
    }}>
      <Stack direction={"row"} justifyContent={"center"} alignItems={"center"} width={"100%"}
             spacing={2}>
        {owner !== "" && customerProfile &&
            <ContextMenu menuItems={[
              <ContentButton title={"View Profile"} onClick={() => {
                navigate(get_customer_url(customerProfile.id))
              }}></ContentButton>,
              <ContentButton title={"Remove Filter"} onClick={() => {
                setSearchParams((searchParams) => {
                  searchParams.set("o", "");
                  return searchParams;
                })
              }}></ContentButton>,
            ]}>
            <ProfileAvatar title={customerProfile.name}
                           avatarUrl={customerProfile.avatarUrl}
                           onClick={() => {
                             setSearchParams((searchParams) => {
                               searchParams.set("o", "");
                               return searchParams;
                             })
                           }}
            />
            </ContextMenu>
        }
        <ClearableTextField label={"Search"} value={currentQuery}
                            onSubmit={() => {

                              setSearchParams((searchParams) => {
                                searchParams.set("q", currentQuery);
                                return searchParams;
                              })

                            }} setValue={setCurrentQuery} endAdornment={<SearchIcon/>}/>
      </Stack>
      <Stack direction={"row"} justifyContent={"space-between"}>
        <Stack direction={"row"} justifyContent={"start"}>
          <MenuButton title={engineToTitle(engine)} menuItems={getEngineMenuItems()}
                      size={"small"}/>
          <MenuButton title={typeToTitle(type)} menuItems={getTypeMenuItems()} size={"small"}/>
          {loginResponse?.userId &&
              <MenuButton title={getUserMenuLabel()} menuItems={getUserMenuItems()}
                          size={"small"}/>}

        </Stack>
        <Stack direction={"row"} justifyContent={"end"}>
          <MenuButton title={sortTypeToTitle(sortType)} menuItems={getSorTypeMenuItems()}
                      size={"small"}/>
        </Stack>
      </Stack>
    </Stack>

    <Grid container rowSpacing={{xs: 1.5, sm: 1.5, md: 1, lg: 1, xl: 1}}
          columnSpacing={{xs: 1, sm: 1, md: 2, lg: 2, xl: 2}}
          width={"100%"}
          marginTop={"0px"}>

      {nodeTrees.map((nodeTree: NodeTreeProfile, index) => {
        return <Grid size={{
          xs: 6,
          sm: 6,
          md: 3,
          lg: 3,
          xl: 12 / 5
        }} key={nodeTree.id}>
          <NodeTreeItemProfile profile={nodeTree}/>
        </Grid>;
      })}

      {/*// <ListComponent data={nodeTrees} renderItem={(nodeTree:NodeTreeProfile, index) => {*/}
      {/*//   return <Grid item xs={6} sm={6} md={3} lg={3} xl={12 / 5} key={nodeTree.id} ><NodeTreeItemProfile profile={nodeTree}/></Grid>;*/}
      {/*// }}/>*/}
    </Grid>

    {totalPages > 1 && <Pagination
        count={totalPages}
        page={currentPage + 1} onChange={(event, page) => {

      setSearchParams((searchParams) => {
        searchParams.set("p", "" + (page - 1));
        return searchParams;
      })
    }}/>}


  </Stack>
};

export default NodeTreeStoreWidget;