import {Stack, StackProps} from "@mui/material";
import React from "react";
import {Typography} from "../wrapper/MuiWrappers";


type TextIconProps = {
  icon: React.ReactNode;
} & StackProps;

const TextIcon = ({icon, children, sx, ...rest}: TextIconProps) => {

  const iconWithCustomFontSize = React.cloneElement(icon as React.ReactElement, {
    sx: {
      ...((icon as React.ReactElement).props.sx),
      fontSize: {
        xs: "1rem",
        sm: "1rem",
        md: "1rem",
        lg: "1.2rem",
        xl: "1.2rem",
      },
    },
  });

  return <Stack direction="row" alignItems="center" gap={0.3} sx={{...sx}} {...rest}>
    {iconWithCustomFontSize}
    <Typography variant={"body1"} sx={{
      fontSize: {
        xs: "0.8rem",
        sm: "0.8rem",
        md: "0.8rem",
        lg: "1rem",
        xl: "1rem",
      },
    }}>{children}</Typography>
  </Stack>;
}

export default TextIcon;