import {Box, Stack, StackProps} from "@mui/material";
import {Link} from "react-router-dom";
import React, {useState} from "react";
import Skeleton from "@mui/material/Skeleton";
import ThumbUpIcon from "@mui/icons-material/ThumbUp";
import RemoveRedEyeIcon from "@mui/icons-material/RemoveRedEye";
import DownloadIcon from "@mui/icons-material/Download";
import {toReadableNumber} from "../../../api/util/StringUtil";
import TextIcon from "../../../component/text/TextIcon";
import {Typography} from "../../../component/wrapper/MuiWrappers";
import OneOf from "../../../component/container/OneOf";


type CoverCardProps = {
  targetUrl: string;
  onClickRoute?: (event: React.MouseEvent<HTMLAnchorElement>) => void;
  owner?: string;
  title?: string;
  description?: string;
  aspectRatio?: number;
  coverImageUrl?: string;
  altImg?: any;
  radius?: number;
  like?: number;
  view?: number;
  download?: number;
  SkeletonSx?: any;
} & StackProps;

const CoverCard = ({
                     targetUrl,
                     onClickRoute,
                     owner,
                     coverImageUrl,
                     altImg,
                     title,
                     description,
                     aspectRatio = 16 / 9,
                     radius = 1,
                     like = 0,
                     view = 0,
                     download = 0,
                     sx,
                     SkeletonSx,
                     ...rest
                   }: CoverCardProps) => {

  const [imageLoaded, setImageLoaded] = useState(coverImageUrl === void 0); // 用于追踪图片是否加载完成
  const [isHovered, setIsHovered] = useState(false);
  const compactView = toReadableNumber(like).length  + toReadableNumber(view).length + toReadableNumber(download).length > 14;
  const noCounter = like === 0 && view === 0 && download === 0;
  return (
      <Stack
          maxWidth={"auto"}
          spacing={1}

          onMouseEnter={() => setIsHovered(true)} // 当鼠标进入Card时设置isHovered为true
          onMouseLeave={() => setIsHovered(false)} // 当鼠标离开Card时设置isHovered为false
          sx={{...sx}}
          {...rest}>
        <Box style={{position: 'relative', width: "auto", height: 'auto'}}>
          {!imageLoaded && (
              <Skeleton variant="rectangular"
                        sx={{
                          objectFit: 'cover',
                          width: "100%", // Ensure full width
                          height: "auto", // Maintain aspect ratio
                          aspectRatio: aspectRatio,
                          ...SkeletonSx,
                        }}/>
          )}
          <Link to={targetUrl} onClick={(event) => {
            if (onClickRoute) {
              onClickRoute(event);
            }
          }}>
            <OneOf index={coverImageUrl === void 0 ? 0 : 1}>
            {altImg}
            <Box component={"img"} src={coverImageUrl}
                 onLoad={() => setImageLoaded(true)}
                 borderRadius={radius}
                 sx={{
                   display: imageLoaded ? 'block' : 'none',
                   objectFit: 'cover', // 保证图片覆盖整个区域
                   width: "100%", // Ensure full width
                   height: "auto", // Maintain aspect ratio
                   aspectRatio: aspectRatio, // Maintain aspect ratio
                 }}>
            </Box>
            </OneOf>

          </Link>
          {imageLoaded && !noCounter && (
              <Stack direction={"row"}
                     borderRadius={radius}
                     sx={{
                       position: 'absolute',
                       bottom: 0,
                       left: 0,
                       color: 'white',
                       background: 'linear-gradient(to bottom, rgba(0,0,0,0) 0%, rgba(0,0,0,0.8) 100%)', // 从上到下的渐变背景
                       width: '100%',
                       textAlign: 'start',
                       paddingY: '6px',
                       opacity: isHovered ? 0 : 1,
                     }}
                     style={{transition: 'opacity 0.5s ease'}}
              >
                <Stack
                    direction={"row"}
                    justifySelf={"flex-start"}
                    alignItems={"center"}
                    flexGrow={1}
                    spacing={1}
                    paddingLeft={2}
                >
                  <TextIcon icon={<ThumbUpIcon/>}>{toReadableNumber(like)}</TextIcon>
                </Stack>
                <Stack
                    direction="row"
                    spacing={1}
                >
                  <Stack direction="row"
                         alignItems="center"
                         spacing={1}
                         paddingRight={2}>
                    <TextIcon sx={{
                      display: {
                        xs: "none",
                        sm: "none",
                        md: "none",
                        lg: compactView ? "none" : "flex"
                      }
                    }}
                              icon={<RemoveRedEyeIcon
                                  fontSize={"small"}/>}>{toReadableNumber(view)}</TextIcon>
                    <TextIcon
                        sx={{
                          display: {
                            xs: "none",
                            sm: "none",
                            md: "none",
                            lg: compactView ? "none" : "flex"
                          }
                        }}
                        icon={<DownloadIcon
                            fontSize={"small"}/>}>{toReadableNumber(download)}</TextIcon>
                    <TextIcon sx={{
                      display: {
                        md: "flex",
                        lg: compactView ? "flex" : "none"
                      }
                    }} icon={<RemoveRedEyeIcon/>}>{toReadableNumber(view)}</TextIcon>
                  </Stack>
                </Stack>
              </Stack>)}
        </Box>
        <Stack direction={"column"} maxWidth={"100%"}>

          <Typography
              gutterBottom
              variant="body1" // Use a smaller variant than h5
              textAlign={"left"}
              fontWeight={
                {xs: "bold", sm: "bold", md: "bold", lg: "bold", xl: "bold"}
              }
              sx={{
                display: '-webkit-box',
                overflow: 'hidden',
                WebkitBoxOrient: 'vertical',
                WebkitLineClamp: {xs: 1, sm: 1, md:1, lg:1, xl: 1},
                lineHeight: '1em', // Limit height to 2.5 lines
                fontSize: {
                  xs: "12px", sm: "12px", md: "15px", lg: '15px', xl: '15px'
                },
                height: "2em",
                textOverflow: 'ellipsis',
                color: "black"
              }}
          >
            {title}
          </Typography>

          {/*TODO*/}
          <Link to={"#"}><Typography variant="body2"
                                                                    color="text.secondary"
                                                                    textAlign={"left"}
                                                                    style={{cursor: owner ? 'pointer' : 'default'}}
                                                                    sx={{
                                                                      fontSize: {
                                                                        xs: '10px',
                                                                        md: '13px',
                                                                      }
                                                                    }}> {/* Reduce font size */}
            {description}
          </Typography></Link>
        </Stack>
      </Stack>
  );
}

export default CoverCard;