import * as React from 'react';
import {NodeTreeProfile} from "../../../proto/application/nodetreestore/NodeTreeStoreMessage";
import CoverCard from "./CoverCard";
import {get_node_tree_item_url} from "../../../navigation/routerUtil";
import {getDownloadCount, getLikeCount, getViewCount} from "../NodeTreeStoreUtil";
import {NodeTreePreviewDefaultImg} from "./NodeTreePreviewDefaultImg";

type NodeTreeItemProfileProps = {
  profile: NodeTreeProfile
};


export const NodeTreeItemProfile = ({
                                      profile
                                    }: NodeTreeItemProfileProps) => {

  return <CoverCard
    title={profile.name}
    targetUrl = {get_node_tree_item_url(profile.id)}
    aspectRatio={1}
    view={Math.max(getViewCount(profile), 1)}
    download={getDownloadCount(profile)}
    SkeletonSx={{width: 200, height: 200}}
    like={getLikeCount(profile)}
    altImg={<NodeTreePreviewDefaultImg type={profile.type} radius={1}
                                       height={"auto"} width={"100%"} preserveAspectRatio={"1"}
    /> }
    coverImageUrl={profile.avatarUrl} />
}

